<template>
  <!---->
  <div v-if="tableData != null">
    <!---->
    <el-table
      :max-height="this.screenHeight - 165"
      :data="tableData"
      style="width: 100%;height: 100%;"
      ><el-table-column prop="id" label="" width="80"></el-table-column
      ><el-table-column
        prop="date"
        label=""
        width="190"
      ></el-table-column>
      <el-table-column prop="cno" label="ID" width="140"> </el-table-column>
      <el-table-column prop="sid" label="ID" width="90"></el-table-column>
      <el-table-column
        prop="staff"
        label=""
        width="110"
      ></el-table-column>
      <el-table-column label="" width="160">
        <template slot-scope="scope">
          <el-button
            :disabled="scope.row.status > 1"
            @click.native.prevent="confirmOrder(scope.row.id, scope.row)"
            size="small"
          >
            <span v-show="scope.row.status > 1"></span>
            
          </el-button>
          <el-button
            :disabled="scope.row.status < 2"
            style="float: right"
            @click.native.prevent="
              closeOrder(scope.row.id, scope.$index, tableData)
            "
            type="success"
            size="small"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  name: "Order",
  props: ["screenHeight"],
  methods: {
    getData() {
      let that = this;
      // VuethisVueaxiosthisundefined
      //  let that = this
      // thisthatthat
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/orders")
        .then(response => {
          that.tableData = response.data;
          console.log("");
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {});
    },
    confirmOrder(id, row) {
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.acceptOrder(id, row);
        })
        .catch(() => {
          this.$message({
            type: "info",
            offset: 66,
            message: ""
          });
        });
    },
    acceptOrder(id, row) {
      let that = this;
      //servletjsonqsurl
      let deleteno = {
        method: "accept",
        id: id
      };
      axios
        // eslint-disable-next-line no-undef
        .post(hxf_conf.BaseUrl + "/api/orders", qs.stringify(deleteno))
        .then(response => {
          if (response.status != 200) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "warning"
            });
            console.log("", id, response.status);
          } else {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "success"
            });
            console.log("", id, response.status);
            row.status = 2;
          }
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: ", " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: ", " + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {
          setTimeout(() => {
            that.getData();
          }, 500);
        });
    },
    closeOrder(id, index, rows) {
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.doClose(id, index, rows);
        })
        .catch(() => {
          this.$message({
            type: "info",
            offset: 66,
            message: ""
          });
        });
    },
    doClose(id, index, rows) {
      //servletjsonqsurl
      let closeno = {
        method: "close",
        id: id
      };
      axios
        // eslint-disable-next-line no-undef
        .post(hxf_conf.BaseUrl + "/api/orders", qs.stringify(closeno))
        .then(response => {
          if (response.status === 205) {
            this.$message({
              showClose: true,
              message: " ID",
              offset: 66,
              type: "warning"
            });
            console.log("", response.status);
          } else if (response.status === 206) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "warning"
            });
            console.log("", response.status);
          } else if (response.status === 207) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "error"
            });
            console.log("", response.status);
          } else if (response.status !== 200) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "error"
            });
            console.log("", response.status);
          } else {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "success"
            });
            console.log("", response.status);
            setTimeout(() => {
              //  echart 
              rows.splice(index, 1);
              this.getData();
            }, 500);
          }
        });
    }
  },
  created() {
    //
    // this.getScreenHeight();
    this.getData();
  },
  data() {
    return {
      eidtindex: "",
      loading: true,
      //axiosresponsetableDatanull
      tableData: null
    };
  }
};
</script>

<style scoped></style>
