<template>
  <!---->
  <div v-if="tableData != null">
    <!---->
    <el-table
      show-summary
      :summary-method="getSummaries"
      :max-height="this.screenHeight - 165"
      :data="
        tableData.filter(
          data =>
            !search || data.cno.toLowerCase().includes(search.toLowerCase())
        )
      "
      style="width: 100%;height: 100%;"
      ><el-table-column prop="id" label="" width="80"></el-table-column
      ><el-table-column
        prop="date"
        label=""
        width="190"
      ></el-table-column>
      <el-table-column prop="cno" label="ID" width="140"> </el-table-column>

      <el-table-column
        prop="method"
        label=""
        width="120"
      ></el-table-column>
      <el-table-column prop="times" label="" width="90"> </el-table-column>
      <el-table-column prop="sid" label="ID" width="90"></el-table-column>
      <el-table-column prop="staff" label="ID" width="90"></el-table-column>
      <el-table-column prop="money" label="" width="100">
        <template slot-scope="scope">
          <el-tag disable-transitions>{{ scope.row.money }}</el-tag>
        </template></el-table-column
      >

      <el-table-column
        fixed="right"
        label=""
        width="130"
        v-if="this.showdelete"
      >
        <template slot="header">
          <input
            type="text"
            v-model="search"
            placeholder="ID"
            class="el-input--mini el-input__inner"
            style="height:30px; line-height: 30px;"
          />
        </template>
        <template slot-scope="scope">
          <el-button
            style="float: right"
            @click.native.prevent="
              confirmDelete(scope.row.id, scope.$index, tableData)
            "
            type="danger"
            size="small"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  name: "MoneyList",
  props: ["screenHeight", "showdelete", "showeidt"],
  methods: {
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        if (index < 4) {
          sums[index] = " ";
          return;
        }
        if (4 < index && index < 7) {
          sums[index] = " ";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 4) {
            sums[index] += " ";
          }
          if (index === 7) {
            sums[index] += " ";
          }
        } else {
          sums[index] = "N/A";
        }
      });

      return sums;
    },
    confirmDelete(id, index, rows) {
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.deleteRow(id, index, rows);
        })
        .catch(() => {
          this.$message({
            type: "info",
            offset: 66,
            message: ""
          });
        });
    },
    deleteRow(id, index, rows) {
      let that = this;
      //servletjsonqsurl
      let deleteno = {
        method: "delete",
        id: id
      };
      axios
        // eslint-disable-next-line no-undef
        .post(hxf_conf.BaseUrl + "/api/moneyapi", qs.stringify(deleteno))
        .then(response => {
          if (response.status != 200) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "warning"
            });
            console.log("", id, response.status);
          } else {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "success"
            });
            console.log("", id, response.status);
            rows.splice(index, 1);
          }
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: ", " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: ", " + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {});
    }
  },
  created() {
    //
    // this.getScreenHeight();
    let that = this;
    // VuethisVueaxiosthisundefined
    //  let that = this
    // thisthatthat
    axios
      // eslint-disable-next-line no-undef
      .get(hxf_conf.BaseUrl + "/api/moneyapi")
      .then(response => {
        that.tableData = response.data;
        console.log("");
      })
      .catch(function(error) {
        try {
          if (error.response.status === 405) {
            console.log(" 405");
          } else {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        } catch (e) {
          console.log("", error);
          that.$message({
            showClose: true,
            message: " " + error,
            offset: 66,
            type: "warning"
          });
        }
      })
      .finally(function() {});
  },
  data() {
    return {
      //
      search: "",
      eidtindex: "",
      loading: true,
      //axiosresponsetableDatanull
      tableData: null
    };
  }
};
</script>

<style scoped></style>
