<template>
  <!---->
  <el-form
    label-position="left"
    ref="form"
    :model="form"
    label-width="80px"
    style="max-width: 800px;"
    :rules="rules"
  >
    <el-form-item label=" ID" prop="id" required>
      <el-select
        filterable
        v-model="form.id"
        placeholder=""
        style="width: 100%"
      >
        <el-option
          v-for="item in cnoList"
          :key="item.cno"
          :label="'ID-' + item.cno + '  ' + item.cname"
          :value="item.cno"
        >
        </el-option>
      </el-select>
    </el-form-item>

    <el-form-item label="" prop="money">
      <el-slider
        :max="max"
        :min="min"
        v-model="form.money"
        show-input
      ></el-slider>
    </el-form-item>

    <!---->
    <!--    <el-form-item label="AdminKey" prop="pass" v-show="false">-->
    <!--      <el-input-->
    <!--        placeholder=""-->
    <!--        v-model="form.pass"-->
    <!--        show-password-->
    <!--      ></el-input>-->
    <!--    </el-form-item>-->

    <el-form-item>
      <el-button style="float: right" type="primary" @click="onSubmit('form')"
        ></el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import md5 from "js-md5";
import qs from "qs";

export default {
  name: "AddMoney",
  data() {
    return {
      max: 500,
      min: 1,
      cnoList: [],
      form: {
        id: "",
        money: 0,
        pass: ""
      },
      //
      rules: {
        id: [{ required: true, message: " ID", trigger: "change" }],
        pass: [
          {
            min: 6,
            max: 20,
            message: " 6  20 ",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getCnoList() {
      let that = this;
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/listmembers")
        .then(response => {
          that.cnoList = response.data;
          console.log("");
          // console.log(that.serviceList);
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        });
    },
    onSubmit(formName) {
      //
      this.$refs[formName].validate(valid => {
        if (!valid) {
          console.log("error submit!!");
        } else {
          //post
          let that = this;
          //servletjsonqsurl
          let postform = {
            method: "income",
            id: this.form.id,
            money: this.form.money
            // md5
            // pass: md5("huxiaofan" + this.form.pass)
          };
          axios
            .post(
              // eslint-disable-next-line no-undef
              hxf_conf.BaseUrl + "/api/moneyapi",
              qs.stringify(postform)
            )
            .then(response => {
              if (response.status != 200) {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "warning"
                });
                console.log("", response.status);
              } else {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "success"
                });
                console.log("", response.status);
              }
            })
            .catch(function(error) {
              try {
                if (error.response.status === 405) {
                  console.log(" 405");
                } else {
                  console.log("", error);
                  that.$message({
                    showClose: true,
                    message: ", " + error,
                    offset: 66,
                    type: "warning"
                  });
                }
              } catch (e) {
                console.log("", error);
                that.$message({
                  showClose: true,
                  message: ", " + error,
                  offset: 66,
                  type: "warning"
                });
              }
            })
            .finally(function() {
              //
              that.resetForm("form");
            });
          console.log("submit!");
          // console.log(postform);
        }
      });
    }
  },
  created() {
    this.getCnoList();
  }
};
</script>

<style scoped></style>
