<template>
  <!---->
  <div v-if="tableData != null">
    <!---->
    <el-table
      :max-height="this.screenHeight - 165"
      :data="
        tableData.filter(
          data =>
            !search || data.cname.toLowerCase().includes(search.toLowerCase())
        )
      "
      style="width: 100%;height: 100%;"
    >
      <el-table-column prop="cno" label="ID" width="150"> </el-table-column>
      <el-table-column prop="cname" label="" width="120">
        <template slot-scope="scope">
          <!--span  input -->
          <el-input
            placeholder=""
            v-show="scope.row.cno == eidtindex && showeidt"
            v-model="scope.row.cname"
          ></el-input>
          <span v-show="scope.row.cno != eidtindex || showeidt == false">{{
            scope.row.cname
          }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="csex" label="" width="90">
        <template slot-scope="scope">
          <!--span  input -->

          <el-select
            name="cars"
            v-show="scope.row.cno == eidtindex && showeidt"
            v-model="scope.row.csex"
            :value="scope.row.csex"
          >
            <!--elmselect-->
            <el-option value=""></el-option>
            <el-option value=""></el-option>
          </el-select>

          <span v-show="scope.row.cno != eidtindex || showeidt == false">{{
            scope.row.csex
          }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="caddress" label="" width="200">
        <template slot-scope="scope">
          <!--span  input -->
          <el-input
            placeholder=""
            v-show="scope.row.cno == eidtindex && showeidt"
            v-model="scope.row.caddress"
          ></el-input>
          <span v-show="scope.row.cno != eidtindex || showeidt == false">{{
            scope.row.caddress
          }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="cregtime" label="" width="215">
        <template slot-scope="scope">
          <!--span  input -->
          <el-date-picker
            value-format="yyyy-MM-dd HH:mm:ss"
            v-show="scope.row.cno == eidtindex && showeidt"
            v-model="scope.row.cregtime"
            type="datetime"
            placeholder=""
            style="width: 200px;"
          >
          </el-date-picker>
          <span v-show="scope.row.cno != eidtindex || showeidt == false">{{
            scope.row.cregtime
          }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="cphone" label="" width="140">
        <template slot-scope="scope">
          <!--span  input -->
          <el-input
              placeholder=""
              v-show="scope.row.cno == eidtindex && showeidt"
              v-model="scope.row.cphone"
          ></el-input>
          <span v-show="scope.row.cno != eidtindex || showeidt == false">{{
              scope.row.cphone
            }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="cmoney" label="" width="100">
      </el-table-column>

      <el-table-column
        fixed="right"
        label=""
        width="130"
        v-if="this.showdelete"
      >
        <template slot="header">
          <input
            type="text"
            v-model="search"
            placeholder=""
            class="el-input--mini el-input__inner"
            style="height:30px; line-height: 30px;"
          />
        </template>
        <template slot-scope="scope">
          <el-button
            style="float: right"
            @click.native.prevent="
              confirmDelete(scope.row.cno, scope.$index, tableData)
            "
            type="danger"
            size="small"
          >
            
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="155"
        v-if="this.showeidt"
      >
        <template slot="header">
          <el-input v-model="search" size="mini" placeholder="" />
        </template>
        <template slot-scope="scope">
          <el-button
            style="float: right;margin-left: 10px;"
            @click.native.prevent="eidtRow(scope.row.cno)"
            type="primary"
            size="small"
          >
            
          </el-button>
          <el-button
            style="float: right"
            @click.native.prevent="saveRow(scope.row)"
            type="success"
            size="small"
            v-show="eidtindex == scope.row.cno"
          >
            <!--IDcno-->
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";

export default {
  name: "MemberList",
  props: ["screenHeight", "showdelete", "showeidt"],
  methods: {
    confirmDelete(cno, index, rows) {
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.deleteRow(cno, index, rows);
        })
        .catch(() => {
          this.$message({
            type: "info",
            offset: 66,
            message: ""
          });
        });
    },
    deleteRow(cno, index, rows) {
      let that = this;
      //servletjsonqsurl
      let deleteno = {
        method: "delete",
        cno: cno
      };
      axios
        // eslint-disable-next-line no-undef
        .post(hxf_conf.BaseUrl + "/api/listmembers", qs.stringify(deleteno))
        .then(response => {
          if (response.status != 200) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "warning"
            });
            console.log("", cno, response.status);
          } else {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "success"
            });
            console.log("", cno, response.status);
            rows.splice(index, 1);
          }
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: ", " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: ", " + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {});
    },
    eidtRow(cno) {
      this.eidtindex = cno;
      console.log("");
    },
    saveRow(row) {
      // console.log(row);
      let that = this;
      //servletjsonqsurl
      let eidtno = {
        method: "modify",
        row: row
      };
      axios
        // eslint-disable-next-line no-undef
        .post(hxf_conf.BaseUrl + "/api/listmembers", qs.stringify(eidtno))
        .then(response => {
          if (response.status != 200) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "warning"
            });
            console.log("", response.status);
          } else {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "success"
            });
            console.log("", response.status);
          }
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: ", " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: ", " + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {});
      //span
      this.eidtindex = "";
      console.log("");
    }
  },
  created() {
    //
    // this.getScreenHeight();
    let that = this;
    // VuethisVueaxiosthisundefined
    //  let that = this
    // thisthatthat
    axios
      // eslint-disable-next-line no-undef
      .get(hxf_conf.BaseUrl + "/api/listmembers")
      .then(response => {
        that.tableData = response.data;
      })
      .catch(function(error) {
        try {
          if (error.response.status === 405) {
            console.log(" 405");
          }else{
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        } catch (e) {
          console.log("", error);
          that.$message({
            showClose: true,
            message: "" + error,
            offset: 66,
            type: "warning"
          });
        }
      })
      .finally(function() {});
  },
  data() {
    return {
      //
      search: "",
      eidtindex: "",
      loading: true,
      //axiosresponsetableDatanull
      tableData: null
    };
  }
};
</script>

<style scoped></style>
