<template>
  <!--<v-chart theme="ovilia-green" :options="polar"/>-->
  <div style="width: 100%;">
    <el-row
      justify="space-around"
      :gutter="10"
      style="padding:0 .5rem 0 .5rem;"
    >
      <el-col :xs="13" :sm="8" :md="6" :lg="6">
        <el-card class="box-card" style="margin-bottom: 1rem; height: 15rem;">
          <div slot="header">
            <span></span>
          </div>
          <el-progress
            type="circle"
            :percentage="hxf_rzl"
          ></el-progress> </el-card
      ></el-col>

      <el-col :xs="11" :sm="16" :md="18" :lg="18"
        ><el-card class="box-card" style="margin-bottom: 1rem; height: 15rem;">
          <div slot="header">
            <span></span>
          </div>
          <el-row justify="space-around" :gutter="10">
            <el-col :xs="0" :sm="8" :md="8" :lg="8">
              <p>
                <el-tag type="success">{{ hxf_wyf }}</el-tag>
                <span class="hidden-sm-and-down"> </span>
              </p>
              <p>
                <el-tag type="info">{{ hxf_fws }}</el-tag>
                <span class="hidden-sm-and-down"> </span>
              </p>
              <p>
                <el-tag type="warning">{{ hxf_liushui }}</el-tag>
                <span class="hidden-sm-and-down"> </span>
              </p> </el-col
            ><el-col :xs="0" :sm="4" :md="4" :lg="4">
              <el-divider direction="vertical" class="el-line"></el-divider>
            </el-col>
            <el-col :xs="24" :sm="12" :md="12" :lg="12">
              <span>
                <p style="font-size: 1.3rem;line-height: 3rem">
                  <span class="hidden-xs-only"></span>
                </p>
                <span style="font-size: 2rem"
                  ><strong>{{ hxf_zhs }} </strong></span
                >
                <span style="font-size: 2rem" class="hidden-xs-only"
                  ><strong></strong></span
                >
              </span>
            </el-col>
          </el-row>
        </el-card></el-col
      >
    </el-row>

    <v-chart ref="chart" :options="option" :auto-resize="true" />
  </div>
</template>

<script>
import Event from "@/uitls/public";
import ECharts from "vue-echarts";
//  ECharts
import "echarts/lib/chart/line";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import axios from "axios";
export default {
  props: ["screenWidth"],
  components: {
    "v-chart": ECharts
  },
  methods: {},
  watch: {
    screenWidth() {
      // console.log(this.screenWidth)
      // 
      this.$refs.chart.resize();
    },
    showbar() {
      // settimeout 
      setTimeout(() => {
        //  echart 
        this.$refs.chart.resize();
      }, 500);
    }
  },
  created() {
    let that = this;
    // VuethisVueaxiosthisundefined
    //  let that = this
    // thisthatthat
    axios
      // eslint-disable-next-line no-undef
      .get(hxf_conf.BaseUrl + "/api/show")
      .then(response => {
        // 
        that.hxf_zhs = response.data[0].count_cno;
        // 
        // eslint-disable-next-line no-undef
        that.hxf_rzl = parseInt((that.hxf_zhs / hxf_conf.hxf_fangjian) * 100);
        //
        that.hxf_wyf = parseInt(response.data[0].count_cmoney);
        //
        that.hxf_fws = parseInt(response.data[1].count_allstimes);
        //
        that.hxf_liushui = parseInt(response.data[1].count_allsmoney);
        // echarts 
        that.option.xAxis[0].data = response.data[2].days;
        // echarts 
        that.option.series[0].data = response.data[3].pay;
        that.option.series[1].data = response.data[3].income;
      })
      .catch(function(error) {
        try {
          if (error.response.status === 405) {
            console.log(" 405");
          } else {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        } catch (e) {
          console.log("", error);
          that.$message({
            showClose: true,
            message: "" + error,
            offset: 66,
            type: "warning"
          });
        }
      })
      .finally(function() {});
  },
  mounted() {
    //
    Event.$on("isshowbar", showbar => {
      this.showbar = showbar; //this=>,thisEvent
    });
    // settimeout 
    try {
      setTimeout(() => {
        //  echart 
        this.$refs.chart.resize();
      }, 500);
    } catch (e) {
      console.log(e);
    }
  },
  data() {
    return {
      showbar: null,
      //
      hxf_rzl: 0,
      //
      hxf_zhs: 0,
      //
      hxf_wyf: 0,
      //
      hxf_fws: 0,
      //
      hxf_liushui: 0,
      option: {
        title: {
          text: ""
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          data: ["", ""]
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: ["", "", "", "", "", "", ""]
          }
        ],
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: "",
            type: "line",
            stack: "",
            areaStyle: {},
            data: [0, 0, 0, 0, 0, 0, 0]
          },
          {
            name: "",
            type: "line",
            stack: "",
            areaStyle: {},
            data: [0, 0, 0, 0, 0, 0, 0]
          }
        ]
      }
    };
  }
};
</script>

<style scoped>
/**
 *  600px400px
 * 
 */
.echarts {
  width: 100%;
  /*height: 100%;*/
}
.el-line {
  height: 9em !important;
}
.box-card p {
  display: block;
  margin-block-start: 0em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
@media (max-width: 800px) {
  .box-card p {
    line-height: 2;
  }
}
</style>
