<template>
  <!---->
  <el-form
    label-position="left"
    ref="form"
    :model="form"
    label-width="80px"
    style="max-width: 800px;"
    :rules="rules"
  >
    <el-form-item label="ID">
      <el-input disabled v-model="form.id"></el-input>
    </el-form-item>
    <el-form-item label="" prop="name">
      <el-input v-model="form.name" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="sex">
      <el-select v-model="form.sex" placeholder="">
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
      </el-select>
    </el-form-item>

    <el-form-item label="" prop="phone">
      <el-input v-model="form.phone"></el-input>
    </el-form-item>

    <!--propithem-->
    <el-form-item label="" required>
      <el-col :span="11">
        <el-form-item prop="date1">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.date1"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
      </el-col>
      <el-col
        class="line"
        :span="2"
        style="width: 5px;margin-right: 5px;margin-left: 5px"
      >
        -
      </el-col>
      <el-col :span="11">
        <el-form-item prop="date2">
          <el-time-picker
            placeholder=""
            v-model="form.date2"
            style="width: 100%;"
          ></el-time-picker>
        </el-form-item>
      </el-col>
    </el-form-item>

    <el-form-item label="" prop="address">
      <el-input type="textarea" v-model="form.address"></el-input>
    </el-form-item>

    <el-form-item label="" prop="money">
      <el-slider :max="max" v-model="form.money" show-input></el-slider>
    </el-form-item>

    <el-form-item label="" prop="pass">
      <el-input
        placeholder=""
        v-model="form.pass"
        show-password
      ></el-input>
    </el-form-item>

    <el-form-item>
      <el-button style="float: right" type="primary" @click="onSubmit('form')"
        ></el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import md5 from "js-md5";
import qs from "qs";

export default {
  name: "AddMember",
  data() {
    return {
      max: 500,
      newuserid: "",
      form: {
        id: "",
        name: "",
        sex: "",
        date1: "",
        date2: "",
        phone: "",
        address: "",
        money: 0,
        pass: ""
      },
      //
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        sex: [{ required: true, message: "", trigger: "change" }],
        phone: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 11,
            max: 13,
            message: " 11  13 ",
            trigger: "blur"
          }
        ],
        date1: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        address: [
          {
            required: true,
            min: 1,
            max: 100,
            message: " 100 ",
            trigger: "blur"
          }
        ],
        pass: [
          {
            required: true,
            min: 6,
            max: 20,
            message: " 6  20 ",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getNewUID() {
      let that = this;
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/addmembers")
        .then(response => {
          that.form.id = response.data[0].newid;
          console.log("ID" + that.form.id);
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("ID", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("ID", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        });
    },
    onSubmit(formName) {
      //
      this.$refs[formName].validate(valid => {
        if (!valid) {
          console.log("error submit!!");
        } else {
          //post
          let that = this;
          //servletjsonqsurl
          let postform = {
            method: "add",
            id: this.form.id,
            name: this.form.name,
            sex: this.form.sex,
            phone: this.form.phone,
            date: this.form.date2,
            address: this.form.address,
            money: this.form.money,
            //md5
            pass: md5("huxiaofan" + this.form.pass)
          };
          axios
            .post(
              // eslint-disable-next-line no-undef
              hxf_conf.BaseUrl + "/api/addmembers",
              qs.stringify(postform)
            )
            .then(response => {
              if (response.status != 200) {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "warning"
                });
                console.log("", response.status);
              } else {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "success"
                });
                console.log("", response.status);
              }
            })
            .catch(function(error) {
              try {
                if (error.response.status === 405) {
                  console.log(" 405");
                } else {
                  console.log("", error);
                  that.$message({
                    showClose: true,
                    message: ", " + error,
                    offset: 66,
                    type: "warning"
                  });
                }
              } catch (e) {
                console.log("", error);
                that.$message({
                  showClose: true,
                  message: ", " + error,
                  offset: 66,
                  type: "warning"
                });
              }
            })
            .finally(function() {
              //
              that.getNewUID();
              that.resetForm("form");
            });
          console.log("submit!");
          // console.log(postform);
        }
      });
    }
  },
  created() {
    this.getNewUID();
  }
};
</script>

<style scoped></style>
