<template>
  <!---->
  <el-form
    label-position="left"
    ref="form"
    :model="form"
    label-width="80px"
    style="max-width: 800px;"
    :rules="rules"
  >
    <el-form-item label="ID" prop="uid">
      <el-select
        filterable
        v-model="form.uid"
        placeholder=""
        style="width: 100%"
      >
        <el-option
          v-for="item in cnoList"
          :key="item.cno"
          :label="'ID-' + item.cno + '  ' + item.cname"
          :value="item.cno"
        >
        </el-option>
      </el-select>

      <!--      <el-input v-model="form.uid" type="number" class="elinput"></el-input>-->
    </el-form-item>

    <el-form-item label="" prop="service" required>
      <el-select
        filterable
        v-model="form.service"
        placeholder=""
        style="width: 100%"
      >
        <el-option
          v-for="item in serviceList"
          :key="item.sid"
          :label="'ID-' + item.sid + '  ' + item.sname"
          :value="item.sid"
        >
        </el-option>
      </el-select>
    </el-form-item>

    <!--propithem-->
    <el-form-item label="" required>
      <el-col :span="11">
        <el-form-item prop="date1">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.date1"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
      </el-col>
      <el-col
        class="line"
        :span="2"
        style="width: 5px;margin-right: 5px;margin-left: 5px"
      >
        -
      </el-col>
      <el-col :span="11">
        <el-form-item prop="date2">
          <el-time-picker
            placeholder=""
            v-model="form.date2"
            style="width: 100%;"
          ></el-time-picker>
        </el-form-item>
      </el-col>
    </el-form-item>

    <el-form-item label="" prop="money">
      <el-slider
        :max="max"
        :min="min"
        v-model="form.times"
        show-input
      ></el-slider>
    </el-form-item>

    <el-form-item label="" prop="staff">
      <el-input
        class="elinput"
        v-model="form.staff"
        placeholder=""
        type="number"
      ></el-input>
    </el-form-item>

    <el-form-item>
      <el-button style="float: right" type="primary" @click="onSubmit('form')"
        ></el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import qs from "qs";

export default {
  name: "MoneyPay",
  data() {
    return {
      cnoList: [],
      serviceList: [],
      max: 5,
      min: 1,
      form: {
        uid: "",
        service: "",
        date1: "",
        date2: "",
        times: 0,
        staff: ""
      },
      //
      rules: {
        uid: [{ required: true, message: " ID", trigger: "change" }],
        service: [
          { required: true, message: "", trigger: "change" }
        ],
        date1: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        staff: [
          {
            required: true,
            min: 1,
            max: 15,
            message: " 15 ",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getCnoList() {
      let that = this;
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/listmembers")
        .then(response => {
          that.cnoList = response.data;
          console.log("");
          // console.log(that.serviceList);
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        });
    },
    getSeriveList() {
      let that = this;
      let getform = {
        want: "slist"
      };
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/servicemanage?", { params: getform })
        .then(response => {
          that.serviceList = response.data;
          console.log("");
          // console.log(that.serviceList);
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        });
    },
    onSubmit(formName) {
      //
      this.$refs[formName].validate(valid => {
        if (!valid) {
          console.log("error submit!!");
        } else {
          //post
          let that = this;
          //servletjsonqsurl
          let postform = {
            method: "pay",
            uid: this.form.uid,
            service: this.form.service,
            date: this.form.date2,
            times: this.form.times,
            staff: this.form.staff
          };
          axios
            .post(
              // eslint-disable-next-line no-undef
              hxf_conf.BaseUrl + "/api/moneyapi",
              qs.stringify(postform)
            )
            .then(response => {
              if (response.status == 205) {
                this.$message({
                  showClose: true,
                  message: " ID",
                  offset: 66,
                  type: "warning"
                });
                console.log("", response.status);
              } else if (response.status == 206) {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "warning"
                });
                console.log("", response.status);
              } else if (response.status == 207) {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "error"
                });
                console.log("", response.status);
              } else if (response.status != 200) {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "error"
                });
                console.log("", response.status);
              } else {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "success"
                });
                console.log("", response.status);
              }
            })
            .catch(function(error) {
              try {
                if (error.response.status === 405) {
                  console.log(" 405");
                } else{
                  console.log("", error);
                  that.$message({
                    showClose: true,
                    message: "" + error,
                    offset: 66,
                    type: "warning"
                  });
                }
              } catch (e) {
                console.log("", error);
                that.$message({
                  showClose: true,
                  message: "" + error,
                  offset: 66,
                  type: "warning"
                });
              }
            })
            .finally(function() {
              //
              that.form = {
                uid: "",
                service: "",
                date1: "",
                date2: "",
                times: 0,
                staff: ""
              };
              that.resetForm("form");
            });
          console.log("submit!");
          // console.log(postform);
        }
      });
    }
  },
  created() {
    this.form.staff = this.isadmin = JSON.parse(this.$store.state.Authorization).eid;
    this.getCnoList();
    setTimeout(() => {
      // 
      this.getSeriveList();
    }, 600);
  }
};
</script>

<style scoped></style>
<style>
.elinput input {
  /*height: 40px!important;*/
  /*padding: 4px 0px!important;*/
  line-height: 1 !important;
}
</style>
