<template>
  <div>
    <!---->
    <el-alert
      style="margin-bottom: 20px;"
      title=""
      type="info"
      description="  /  "
      show-icon
    >
      <!---->
    </el-alert>
    <!---->
    <el-form
      label-position="left"
      ref="newService"
      :rules="rules"
      :inline="true"
      :model="newService"
      class="demo-form-inline"
      v-show="showeidt"
    >
      <el-form-item label=" SID">
        <el-input
          v-model="newService.id"
          placeholder="ID"
          :disabled="true"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="newService.name" placeholder=""></el-input>
      </el-form-item>
      <!--allow-createfilterabledefault-first-option-->
      <el-form-item label="" prop="price">
        <el-select
          type="number"
          v-model="newService.price"
          placeholder=""
          filterable
          allow-create
          default-first-option
        >
          <el-option label="" value="0"></el-option>
          <el-option label="50 " value="50"></el-option>
          <el-option label="100 " value="100"></el-option>
          <el-option label="150 " value="150"></el-option>
          <el-option label="200 " value="200"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="newService.desc"
        >
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('newService')"
          ></el-button
        >
      </el-form-item>
    </el-form>
    <!---->
    <!---->
    <el-row>
      <el-col
        :sm="24"
        :md="11"
        v-for="(item, index) in serviceList"
        :key="index"
        style="margin-left: -10px;"
      >
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span
              ><el-tag effect="dark" type="success" style="margin-right: 10px;"
                >ID{{ item.sid }}</el-tag
              >
              {{ item.sname }}</span
            >
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
              v-show="showdelete"
              @click="confirmDelete(item.sid)"
              ></el-button
            >
          </div>
          <div class="text item">{{ item.sdesc }}</div>
          <div class="text item">
            <el-tag type="warning">{{ item.sprice }} </el-tag>
          </div>
          <div class="text item">
            <el-tag>{{ item.stime }} </el-tag>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";

export default {
  name: "ServiceMenu",
  props: ["showdelete", "showeidt"],
  data() {
    return {
      serviceList: [],
      newService: {
        id: "",
        name: "",
        price: "",
        desc: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 10, message: " 10 ", trigger: "blur" }
        ],
        price: [{ required: true, message: "" }],
        desc: [
          {
            required: true,
            min: 1,
            max: 50,
            message: " 50 ",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    this.getSeriveList();
    setTimeout(() => {
      // 
      this.getNewUID();
    }, 600);
  },
  methods: {
    getSeriveList() {
      let that = this;
      let getform = {
        want: "slist"
      };
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/servicemanage?", { params: getform })
        .then(response => {
          that.serviceList = response.data;
          console.log("");
          // console.log(that.serviceList);
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log(error.response.status);
              console.log("", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        });
    },
    getNewUID() {
      let that = this;
      let getform = {
        want: "sid"
      };
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/servicemanage?", { params: getform })
        .then(response => {
          that.newService.id = response.data[0].newid;
          console.log("ID" + that.newService.id);
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("ID", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("ID", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        });
    },
    onSubmit(formName) {
      //
      this.$refs[formName].validate(valid => {
        if (!valid) {
          console.log("error submit!!");
        } else {
          //post
          let that = this;
          //servletjsonqsurl
          let postform = {
            method: "add",
            sid: this.newService.id,
            name: this.newService.name,
            //
            price: parseFloat(this.newService.price).toFixed(2),
            desc: this.newService.desc,
            time: 0
          };
          axios
            .post(
              // eslint-disable-next-line no-undef
              hxf_conf.BaseUrl + "/api/servicemanage",
              qs.stringify(postform)
            )
            .then(response => {
              if (response.status != 200) {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "warning"
                });
                console.log("", response.status);
              } else {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "success"
                });
                console.log("", response.status);
              }
            })
            .catch(function(error) {
              try {
                if (error.response.status === 405) {
                  console.log(" 405");
                } else {
                  console.log("", error);
                  that.$message({
                    showClose: true,
                    message: "," + error,
                    offset: 66,
                    type: "warning"
                  });
                }
              } catch (e) {
                console.log("", error);
                that.$message({
                  showClose: true,
                  message: "," + error,
                  offset: 66,
                  type: "warning"
                });
              }
            })
            .finally(function() {
              that.getNewUID();
              that.newService = {
                id: "",
                name: "",
                price: "0",
                desc: ""
              };
              that.getSeriveList();
            });
        }
      });
    },
    confirmDelete(sid) {
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.deleteService(sid);
        })
        .catch(() => {
          this.$message({
            type: "info",
            offset: 66,
            message: ""
          });
        });
    },
    deleteService(sid) {
      let that = this;
      //servletjsonqsurl
      let deleteid = {
        method: "delete",
        sid: sid
      };
      axios
        // eslint-disable-next-line no-undef
        .post(hxf_conf.BaseUrl + "/api/servicemanage", qs.stringify(deleteid))
        .then(response => {
          if (response.status != 200) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "warning"
            });
            console.log("", sid, response.status);
          } else {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "success"
            });
            console.log("", sid, response.status);
          }
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: "," + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: "," + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {
          that.getSeriveList();
          setTimeout(() => {
            // 
            that.getNewUID();
          }, 600);
        });
    }
  }
};
</script>

<style scoped>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 100%;
  margin: 10px 20px 10px 10px;
}
</style>
