<template>
  <div>
    <!---->
    <el-form
      label-position="left"
      ref="newStaff"
      :rules="rules"
      :inline="true"
      :model="newStaff"
      class="demo-form-inline"
    >
      <el-form-item label=" EID">
        <el-input
          v-model="newStaff.id"
          placeholder="ID"
          :disabled="true"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="newStaff.name" placeholder=""></el-input>
      </el-form-item>
      <!--allow-createfilterabledefault-first-option-->
      <el-form-item label="" prop="sex">
        <el-select
          type="number"
          v-model="newStaff.sex"
          placeholder=""
          default-first-option
        >
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="pass">
        <el-input
          v-model="newStaff.pass"
          placeholder=""
          show-password
        ></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-switch
          v-model="newStaff.isadmin"
          active-text=""
          inactive-text=""
        >
        </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('newStaff')"></el-button>
      </el-form-item>
    </el-form>
    <!---->

    <!---->
    <div v-if="tableData != null">
      <!---->
      <el-table
        :data="
          tableData.filter(
            data =>
              !search || data.ename.toLowerCase().includes(search.toLowerCase())
          )
        "
        style="width: 100%;height: 100%;"
      >
        <el-table-column prop="eid" label="ID" width="110">
        </el-table-column>
        <el-table-column prop="ename" label="" width="130">
        </el-table-column>
        <el-table-column prop="esex" label="" width="90"> </el-table-column>
        <el-table-column prop="isadmin" label="" width="160">
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot="header">
            <input
              type="text"
              v-model="search"
              placeholder=""
              class="el-input--mini el-input__inner"
              style="height:30px; line-height: 30px;"
            />
          </template>
          <template slot-scope="scope">
            <!--disabled-->
            <el-button
              :disabled="scope.$index === 0"
              style="float: right"
              @click.native.prevent="
                confirmDelete(scope.row.eid, scope.$index, tableData)
              "
              type="danger"
              size="small"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
import md5 from "js-md5";
export default {
  name: "StaffList",
  props: ["screenHeight"],
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    onSubmit(formName) {
      //
      this.$refs[formName].validate(valid => {
        if (!valid) {
          console.log("error submit!!");
        } else {
          //post
          let that = this;
          //servletjsonqsurl
          let postform = {
            method: "add",
            id: this.newStaff.id,
            name: this.newStaff.name,
            sex: this.newStaff.sex,
            //md5
            pass: md5("huxiaofan" + this.newStaff.pass),
            isadmin: this.newStaff.isadmin
          };
          axios
            .post(
              // eslint-disable-next-line no-undef
              hxf_conf.BaseUrl + "/api/staff",
              qs.stringify(postform)
            )
            .then(response => {
              if (response.status != 200) {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "warning"
                });
                console.log("", response.status);
              } else {
                this.$message({
                  showClose: true,
                  message: "",
                  offset: 66,
                  type: "success"
                });
                console.log("", response.status);
              }
            })
            .catch(function(error) {
              try {
                if (error.response.status === 405) {
                  console.log(" 405");
                } else {
                  console.log("", error);
                  that.$message({
                    showClose: true,
                    message: "," + error,
                    offset: 66,
                    type: "warning"
                  });
                }
              } catch (e) {
                console.log("", error);
                that.$message({
                  showClose: true,
                  message: "," + error,
                  offset: 66,
                  type: "warning"
                });
              }
            })
            .finally(function() {
              that.getNewEID();
              that.getStaffList();
              that.resetForm("newStaff");
            });
        }
      });
    },
    getNewEID() {
      let that = this;
      let getform = {
        want: "eid"
      };
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/staff?", { params: getform })
        .then(response => {
          that.newStaff.id = response.data[0].newid;
          console.log("ID" + that.newStaff.id);
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("ID", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("ID", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        });
    },
    getStaffList() {
      let that = this;
      let getform = {
        want: "elist"
      };
      axios
        // eslint-disable-next-line no-undef
        .get(hxf_conf.BaseUrl + "/api/staff?", { params: getform })
        .then(response => {
          that.tableData = response.data;
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: " " + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: " " + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {});
    },

    confirmDelete(eno, index, rows) {
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.deleteRow(eno, index, rows);
        })
        .catch(() => {
          this.$message({
            type: "info",
            offset: 66,
            message: ""
          });
        });
    },

    deleteRow(eno, index, rows) {
      let that = this;
      //servletjsonqsurl
      let deleteno = {
        method: "delete",
        id: eno
      };
      axios
        // eslint-disable-next-line no-undef
        .post(hxf_conf.BaseUrl + "/api/staff", qs.stringify(deleteno))
        .then(response => {
          if (response.status != 200) {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "warning"
            });
            console.log("", eno, response.status);
          } else {
            this.$message({
              showClose: true,
              message: "",
              offset: 66,
              type: "success"
            });
            console.log("", eno, response.status);
            rows.splice(index, 1);
          }
        })
        .catch(function(error) {
          try {
            if (error.response.status === 405) {
              console.log(" 405");
            } else {
              console.log("", error);
              that.$message({
                showClose: true,
                message: "," + error,
                offset: 66,
                type: "warning"
              });
            }
          } catch (e) {
            console.log("", error);
            that.$message({
              showClose: true,
              message: "," + error,
              offset: 66,
              type: "warning"
            });
          }
        })
        .finally(function() {
          that.getNewEID();
        });
    }
  },
  created() {
    //
    this.getStaffList();
    setTimeout(() => {
      // 
      this.getNewEID();
    }, 600);
  },
  data() {
    return {
      //
      search: "",
      loading: true,
      //axiosresponsetableDatanull
      tableData: null,
      newStaff: {
        id: "",
        name: "",
        sex: "",
        pass: "",
        isadmin: false
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 5, message: " 5 ", trigger: "blur" }
        ],
        sex: [{ required: true, message: "" }],
        pass: [
          {
            required: true,
            min: 6,
            max: 20,
            message: " 6  20 ",
            trigger: "blur"
          }
        ]
      }
    };
  }
};
</script>

<style scoped></style>
